/* <----------------------------------------------------------------------------------->*/
/* <------------------------------ SERVICE PAGE STARTS -------------------------------->*/
/* <----------------------------------------------------------------------------------->*/

.service-jumbo {
  background: url(../../Assets/ServicesPage/serviceBanner-1.jpg) no-repeat fixed
    top;
  background-size: cover;
  color: rgb(0, 0, 0);
  min-height: calc(40vh - 66px);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 200px;
  /* position: relative; */
  /* flex: 1;
      resize: both; */
  z-index: -2;
  text-align: center;
}

.service-jumboText {
  text-shadow: 2px 2px 4px #222222;
  font-family: lora;

  background: rgb(
    255,
    255,
    255
  ); /* Fallback for older browsers without RGBA-support */
  background: rgba(5, 106, 189, 0.836);
  padding: 5px;
  border-radius: 8px;
  margin-top: 60px;

  -moz-box-shadow: 3px 3px 5px 6px rgb(240, 240, 240);
  -webkit-box-shadow: 3px 3px 5px 6px #ccc;
  box-shadow: 1px 1px 5px 2px #ccc;
}

#service-jumboText-font-1 {
  font-size: 3rem;
  color: #ffffff;
}

#service-jumboText-font-2 {
  font-size: 1.5rem;
  font-family: Arial, Helvetica, sans-serif;
}

.jmbp-web-services-bg {
  background: linear-gradient(
      rgba(255, 255, 255, 0.856),
      rgba(255, 255, 255, 0.856)
    ),
    url("../../Assets/ServicesPage/serviceBG.jpg") no-repeat fixed center;

  background-size: cover;
  -moz-box-shadow: 1px 1px 5px 2px #ececec;
  -webkit-box-shadow: 1px 1px 5px 2px #ececec;
  box-shadow: 1px 1px 5px 2px #ececec;
  /* background-color: cornsilk; */
  margin-top: 30px;
  padding-bottom: 70px;
  padding-top: 30px;
}

.services-row {
  margin-bottom: 40px;
}

.jmbp-web-services-para {
  color: black;
  font-family: var(--fontText);
  font-size: 16pt;
  line-height: 2.2rem;
  text-align: center;
  margin-bottom: 10px;
}

#right-para-padding {
  padding: 40px 40px 0px 10px;
}

#left-para-padding {
  padding: 40px 10px 0px 40px;
}

.jmbp-all-service-para-withHeader > h1 {
  font-family: var(--fontHeader);
  font-weight: bold;
  text-transform: uppercase;
  color: var(--header);
}

.jmbp-all-service-para-withHeader > p {
  color: black;
  font-family: var(--fontText);
  font-size: 14pt;
  line-height: 2rem;
}

.jmbp-all-service-Img-div {
  text-align: center;
}

.jmbp-all-service-Img {
  -moz-box-shadow: 1px 1px 5px 2px rgb(170, 169, 169);
  -webkit-box-shadow: 1px 1px 5px 2px rgb(170, 169, 169);
  box-shadow: 1px 1px 5px 2px rgb(170, 169, 169);
}

/* <----------------------------------------------------------------------------------->*/
/* <------------------------------ SERVICE PAGE ENDS -------------------------------->*/
