/*.............................. Navigation Starts..............................*/

.navStyle {
  background-color: white;
  font-size: 11pt;
  font-weight: bold;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.navPadding {
  padding-right: 55px;
}

/*...................................*/
.navBorder {
  width: 100%;
  font-family: lora;
  text-transform: uppercase;
  font-size: 14pt;
  font-weight: bold;
  text-align: center;
  margin: 3rem auto 0;
}

.navBorder > span {
  margin: 0;
  padding: 19px 5px 19px 5px;
  border: 0 none;
  position: relative;
  outline: none;
  text-decoration: none;
  color: black;
  z-index: 1;
}

.navBorder > span:before {
  content: "";
  background: #cfbdff;
  position: absolute;
  width: 100%;
  height: 5px;
  bottom: 0;
  left: 0;
  border-radius: 5px;
  transform: scaleX(0);
  animation: 1.4s forwards no-hover-v linear;
  animation-fill-mode: forwards;
  z-index: -1;
}

.navBorder > span:hover:before,
.navBorder > span:focus:before {
  animation: 0.5s forwards hover-v linear;
  animation-fill-mode: forwards;
}

@keyframes hover-v {
  0% {
    transform: scaleX(0);
    height: 2px;
  }
  45% {
    transform: scaleX(1.05);
    height: 2px;
  }
  55% {
    height: 2px;
  }
  100% {
    transform: scaleX(1.05);
    height: 3.8rem;
  }
}
@keyframes no-hover-v {
  0% {
    transform: scaleX(1.05);
    height: 3.8rem;
  }
  45% {
    height: 5px;
  }
  55% {
    transform: scaleX(1.05);
    height: 5px;
    opacity: 1;
  }

  100% {
    transform: scaleX(0);
    height: 5px;
    opacity: 0.02;
  }
}
