@media screen and (min-width: 601px) {
  .logoName {
    color: var(--header);
    font-size: 25pt;
  }

  .logo {
    font-size: 25pt;
    font-family: Georgia, "Times New Roman", Times, serif;
    font-weight: lighter;
    color: var(--header);
    margin-top: -1rem;
  }
  .logoJmbp {
    font-family: imprint mt shadow;
    font-size: 15pt;
    color: var(--header);
  }
  .logo-Paragraph {
    font-size: 16pt;

    font-family: cursive;
    color: var(--header);
    margin-top: -0.35rem;
    margin-bottom: -0.5rem;
  }
}

@media screen and (max-width: 600px) {
  .logoName {
    color: var(--header);
    font-size: 14pt;
  }

  .logo {
    font-size: 12pt;
    font-family: Georgia, "Times New Roman", Times, serif;
    font-weight: lighter;
    color: var(--header);
    margin-top: -1rem;
  }
  .logoJmbp {
    font-family: imprint mt shadow;
    font-size: 10pt;
  }
  .logo-Paragraph {
    font-size: 10pt;
    font-family: cursive;
    color: var(--header);
    margin-top: -0.35rem;
    margin-bottom: -0.5rem;
  }
}
