* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --primaryColor: #6776ff;
  --header: #025175;
  --description: #000000;
  --mainWhite: #fff;
  --offWhite: #f7f7f7;
  --mainBlack: #222;
  --mainGrey: #ececec;
  --darkGrey: #cfcfcf;
  --mainTransition: all 0.3s linear;
  --mainSpacing: 3px;
  --lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
  --fontHeader: Lora;
  --fontText: "Vollkorn", serif;
}

/* <----------------------------------------------------------------------------------->*/
/* <-------------------------------- HOME PAGE STARTS --------------------------------->*/
/* <----------------------------------------------------------------------------------->*/
.header h1 {
  font-family: var(--fontHeader);
  font-weight: bold;
  text-transform: uppercase;
  margin-top: 35px;
  text-align: center;
  color: var(--header);
}

.header h2 {
  font-family: var(--fontHeader);
  font-weight: bold;
  text-transform: uppercase;
  margin-top: 35px;
  text-align: center;
  color: var(--header);
}

.header h5 {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  text-align: center;
  line-height: 1.5em;
  letter-spacing: 2px;
  color: var(--description);
  font-size: 12pt;
}

.divLine {
  width: 10rem;
  height: 5px;
  background-color: var(--primaryColor);
  margin: 1.7rem auto;
}

/*.............................. Our Services Starts..............................*/

.services-box {
  padding: 10px;
}

.services-box h4 {
  text-align: center;
  text-transform: uppercase;
  font-size: 1.4rem;
}

.services-box p {
  margin-top: 15px;
  font-family: var(--fontText);
  text-align: center;
  line-height: 1.5em;
  letter-spacing: 1.5px;
  color: var(--description);
  font-size: 13pt;
}

.service-icons {
  font-size: 3rem;
  /* color: #ff7301; */
  text-align: center;
}
/* 
.service-icons:hover {
  color: rgb(255, 1, 149);
} */

.services-box:hover {
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  font-weight: bold;
  padding: 10px;
  cursor: pointer;
  transition: var(--mainTransition);
  -moz-box-shadow: 3px 3px 5px 6px #ccc;
  -webkit-box-shadow: 3px 3px 5px 6px #ccc;
  box-shadow: 3px 3px 5px 6px #ccc;
}

/*.............................. Our Services Finish..............................*/

/*.............................. What makes us Starts..............................*/

.what-makes-us {
  margin-top: 30px;
  padding-bottom: 70px;
  padding-top: 30px;
  background: url(./Assets/Home/jmbp-soft-img-1.jpg) no-repeat fixed bottom;

  /* filter: alpha(opacity=50);
  filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0.5);
  opacity: 0.5;
  position: relative; */
}

.what-makes-us-para {
  font-family: var(--fontText);
  font-size: 15pt;
  letter-spacing: 1px;
  line-height: 1.5em;
  background-color: rgba(235, 235, 235, 0.863);
  padding: 20px;
  border-radius: 15px;
  margin: 10px;
  border-style: solid;
  border-color: seashell;

  -moz-box-shadow: 3px 3px 5px 6px #ccc;
  -webkit-box-shadow: 3px 3px 5px 6px #ccc;
  box-shadow: 3px 3px 5px 6px #ccc;
}

