    background: url(./Assets/Home/jmbp-soft-img-2.jpg) no-repeat fixed bottom;
    filter: alpha(opacity=50);
    filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0.5);
    opacity: 0.5;
    position: absolute;
  } */

/*.............................. What makes us Finish..............................*/

/*.............................. Technology Starts..............................*/
.technology-img {
  width: 150px;
  padding: 15px 15px 55px 15px;
}

/*.............................. Technology Finish..............................*/

/*.............................. Why Choose Us Starts..............................*/
.why-choose-us {
  margin-top: 30px;
}
.why-choose-us-image {
  text-align: center;
  transition: var(--mainTransition);
  -moz-box-shadow: 3px 3px 5px 6px #ccc;
  -webkit-box-shadow: 3px 3px 5px 6px #ccc;
  box-shadow: 3px 3px 5px 6px #ccc;
}
.why-choose-us-text > ul {
  font-family: var(--fontText);
  color: rgb(2, 0, 32);
  font-size: 13pt;
  /* text-shadow: 2px 3px 9px rgb(132, 171, 255); */
  letter-spacing: 0.5px;
  line-height: 1.9rem;
  font-weight: normal;
  padding-left: 1rem;
  list-style-type: none;
}

.why-choose-us-tab {
  margin-top: 40px;
  margin-bottom: 40px;
  background-color: var(--primaryColor);
  border: rgb(221, 221, 221) solid 1px;
  border-radius: 10px;
  padding: 20px;
  -moz-box-shadow: 3px 3px 5px 6px #ccc;
  -webkit-box-shadow: 3px 3px 5px 6px #ccc;
  box-shadow: 1px 1px 5px 3px #ccc;
}

/*.............................. Why Choose Us Finishes..............................*/

/*.............................. Request Quote Starts..............................*/
.request-quote {
  margin-top: 50px;
  padding: 70px 50px 90px 50px;
  background-color: #ae18d3;
  text-align: center;
  -moz-box-shadow: 10px 10px 10px 10px rgb(143, 26, 167);
  -webkit-box-shadow: 10px 10px 10px 10px rgb(143, 26, 167);
  box-shadow: 10px 10px 10px 10px rgb(143, 26, 167);
}

.request-quote-text {
  font-family: var(--fontText);
  font-size: 26pt;
  color: aliceblue;
  letter-spacing: 0.5px;
  line-height: 3rem;
}
.request-button {
  margin-top: 25px;
}
.request-button > span {
  font-weight: bold;
  background-color: rgb(145, 245, 245);
  padding: 13px;
  border-radius: 15px;
  -moz-box-shadow: 1px 1px 5px 2px #ccc;
  -webkit-box-shadow: 1px 1px 5px 2px #ccc;
  box-shadow: 1px 1px 5px 2px #ccc;
}

.request-button > span:hover {
  background-color: rgb(245, 235, 145);
  cursor: pointer;
}

.underline--magical {
  padding-left: 5px;
  padding-right: 6px;
  background-image: linear-gradient(120deg, #ffd382 0%, #9cfcff 100%);
  background-repeat: no-repeat;
  background-size: 100% 0.2em;
  background-position: 0 88%;
  transition: background-size 0.25s ease-in;
}

.underline--magical:hover {
  background-size: 100% 88%;
  color: black;
  cursor: pointer;
}
/*.............................. Request Quote Finishes..............................*/

/*.............................. About JMBP Web Starts..............................*/

.about-jmbp-web-para-1 {
  font-family: var(--fontText);
  font-size: 14pt;
  border-right: 1px dashed var(--primaryColor);
}

.about-jmbp-web-para-2 {
  font-family: var(--fontText);
  font-size: 14pt;
}

.about-jmbp-web-img {
  margin-top: 35px;
  -moz-box-shadow: 1px 1px 5px 2px #ccc;
  -webkit-box-shadow: 1px 1px 5px 2px #ccc;
  box-shadow: 1px 1px 5px 2px #ccc;
}

.about-jmbp-web-img:hover {
  border: rgb(247, 246, 193) 2px solid;
}

@media screen and (max-width: 600px) {
  .about-jmbp-web-para-1 {
    font-family: var(--fontText);
    font-size: 14pt;
    border-bottom: 1px solid #ccc;
    text-align: center;
    padding-bottom: 15px;
    margin-bottom: 20px;
  }
}

/*.............................. About JMBP Web Finishes..............................*/

/*.............................. Premier Web Agency Starts..............................*/

/* .jmbp-web-premier-bg-master {
  position: relative;
  z-index: 1;
} */

.jmbp-web-premier-bg {
  background: linear-gradient(
      rgba(255, 255, 255, 0.7),
      rgba(255, 255, 255, 0.7)
    ),
    url("./Assets/jmbp-web-premier-bg.jpg") no-repeat fixed center;

  background-size: cover;
  -moz-box-shadow: 1px 1px 5px 2px #ececec;
  -webkit-box-shadow: 1px 1px 5px 2px #ececec;
  box-shadow: 1px 1px 5px 2px #ececec;
  /* background-color: cornsilk; */
}
.jmbp-web-premier-para {
  color: black;
  font-family: var(--fontText);
  font-size: 14pt;
  line-height: 2.2rem;
}

#jmbp-web-premier-para-last {
  padding-bottom: 25px;
}

.jmbp-web-premier-para > div > h4 {
  font-family: var(--fontHeader);
  margin-top: 25px;
  color: var(--header);
  font-weight: bold;
}

.jmbp-web-premier-img {
  margin-top: 25px;
  -moz-box-shadow: 1px 1px 5px 2px #ccc;
  -webkit-box-shadow: 1px 1px 5px 2px #ccc;
  box-shadow: 1px 1px 5px 2px #ccc;
}

.jmbp-web-premier-header-icon {
  font-size: 2.5rem;
}
/*.............................. Premier Web Agency Finishes..............................*/

/* <----------------------------------------------------------------------------------->*/
/* <-------------------------------- HOME PAGE ENDS --------------------------------->*/
/* <----------------------------------------------------------------------------------->*/

.animate__animated.animate__bounce {
  --animate-duration: 2s;
}
