.mainbox {
  background: rgb(255, 255, 255);
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(149, 194, 222, 1) 10%,
    rgba(153, 196, 223, 1) 88%,
    rgba(255, 255, 255, 1) 100%
  );
  margin: auto;
  height: 600px;
  width: auto;
}

.err {
  color: #ffffff;
  font-family: "Nunito Sans", sans-serif;
  text-align: center;
  font-size: 11rem;
  margin-top: 80px;
}

.far {
  font-size: 8.5rem;

  color: #ffffff;
}

.err2 {
  color: #ffffff;
  font-family: "Nunito Sans", sans-serif;
  font-size: 11rem;
}

.msg {
  text-align: center;
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.6rem;
}

a {
  text-decoration: none;
  color: white;
}

a:hover {
  text-decoration: underline;
}
